.app {
  max-width: 600px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
}

.signout-btn {
  margin-bottom: 10px;
}

.chat {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 8px;
  height: 400px;
  overflow-y: auto;
}

.message {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 10px;
  position: relative;
}

.message .text {
  flex: 1;
  margin-right: 10px;
}

.menu-container {
  position: relative;
}

.menu-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
}

.menu {
  position: absolute;
  top: 20px;
  right: 0;
  background: #fff;
  border: 1px solid #ccc;
  padding: 5px;
  display: flex;
  flex-direction: column;
  z-index: 10;
}

.menu button {
  background: none;
  border: none;
  cursor: pointer;
  margin: 2px 0;
}

.send-form {
  display: flex;
  margin-top: 10px;
}

.send-form input {
  flex: 1;
  padding: 5px;
  font-size: 16px;
}

.send-form button {
  padding: 5px 10px;
  font-size: 16px;
}

.auth-container {
  max-width: 400px;
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.auth-container input {
  padding: 8px;
  font-size: 16px;
}

.auth-container button {
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
}

.switch-link {
  color: blue;
  cursor: pointer;
  text-decoration: underline;
}
